<template>
	<view class="box">
		<view class="list-box">
			<template v-for="(item,k) in data_list">
				<view class="list-li" :key="k">
					<view class="left">
						<view class="name">
							{{item.type==1?'个人':'商家'}}：{{item.userName}}
						</view>
						<view class="time">
							{{item.updateDate}} <text class="status" :class="{'text-red':item.status==99}">{{status_map[item.status]}}</text>
						</view>
					</view>
					<navigator :url="'upload?merchantSeqId='+item.seqId" class="btn" v-if="item.status==99">
						再次入驻
					</navigator>
				</view>
			</template>
			<mescroll-empty v-if="data_list.length==0"></mescroll-empty>
		</view>
	</view>
</template>

<script>
	import MescrollEmpty from '@/components/mescroll-uni/components/mescroll-empty.vue';
	export default {
		data() {
			return {
				data_list: [],
				status_map: {
					1: '待审核',
					99: '审核失败',
					100: "审核成功"
				}
			}
		},
		components: {
			MescrollEmpty
		},
		onLoad() {
			this.getList()
		},
		methods: {
			getList() {
				this.$api.get('/merchant/selectByUserSeqId', {
					params: {
						userSeqId: this.$common.getLocalSync('customerSeqId'),
					}
				}).then((res) => {
					if (res.code == 200) {
						if (res.data) {
							this.data_list = res.data
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.list-box {

		// background: white;
		.list-li {
			background: white;
			display: flex;
			align-items: center;
			width: calc(100% - 60rpx);
			padding: 0 30rpx;
			height: 160rpx;
			border-bottom: 1px solid #d7d7d7;
			font-size: 30rpx;

			.left {
				width: 520rpx;
				flex-shrink: 0;

				.name {
					font-size: 34rpx;
					line-height: 90rpx;
				}

				.time {
					font-size: 26rpx;
					color: #666;

					.status {
						margin-left: 20rpx;
						color: $color-primary;
					}
				}
			}

			.btn {
				width: 160rpx;
				height: 60rpx;
				text-align: center;
				line-height: 60rpx;
				border-radius: 40rpx;
				font-size: 28rpx;
				background: $color-primary;
				color: white;
			}
		}
	}
</style>
